import React, { useState } from 'react'
import { Avatar, Button, Paper, Grid, Typography, Container, TextField } from '@material-ui/core';
import { GoogleLogin } from 'react-google-login';
import { useDispatch } from 'react-redux'
import { useHistory } from 'react-router-dom'

import LockOutlinedIcon from '@material-ui/icons/LockOutlined';
import useStyles from './styles.js'
import Input from './Input'
import Icon from './icon'

import { signup, signin } from '../../actions/auth'


const initialSate = {firstName: '', lastName:'', email:'', password:'', confirmPassword:''}
const Auth = () => {

    const classes = useStyles();
    const [showPassword, setShowPassword] = useState(false);
    const [isSignup, setIsSignup] = useState(false);
    const [formData, setFormData] = useState(initialSate);
    const dispatch = useDispatch();
    const history = useHistory();

    const handleSubmit = (e) =>{ 
        e.preventDefault();
        
        if(isSignup){
            dispatch(signup(formData, history))
        }else{
            dispatch(signin(formData, history))
        }

    }

    const handleChange = (e) =>{
        setFormData({...formData, [e.target.name]: e.target.value});
    }

    const swithcMode = () => {
        setIsSignup((prevIsSignup)=> !prevIsSignup);
        setShowPassword(false);
    }    

    const handleShowPassword = () => setShowPassword((prevShowPassword)=> !prevShowPassword);

    const GoogleSuccess = async (res) =>{
       const result = res?.profileObj;
       const token = res?.tokenId;
       try{
            dispatch ( { type:'AUTH', data:{result, token}})
            history.push('/');
       }catch(error){
            console.log(error);
       }

    }

    const GoogleFailure = () =>{
        console.log('google sign in was unsuccessful')
    }

    return (
        <Container component="main" maxWidth="xs">
            <Paper className={classes.paper} elevation={3}>
                <Avatar className={classes.avatar}>
                    <LockOutlinedIcon />
                </Avatar>
                <Typography variant="h5">
                   {isSignup ? 'Sign Up' : 'Sign In' } 
                </Typography>
                <form className={classes.form} onSubmit={handleSubmit}>
                    <Grid container spacing={2}>
                        {
                            isSignup && (
                                <>
                                    <Input name="firstName" label="First Name" handleChange={handleChange} autoFocus half />
                                    <Input name="lastName" label="Last Name" handleChange={handleChange}  half />
                                </>
                                   
                            )
                        }
                        <Input name="email" label="Email Address" handleChange={handleChange} type="email"  />
                        <Input name="password" label="Password" handleChange={handleChange}  handleShowPassword={handleShowPassword} type={showPassword ? 'text' : 'password'}  />
                        {
                            isSignup && (
                                <Input name="confirmPassword" label="Repeat Password" handleChange={handleChange} type="password" />
                            )
                        }
                    </Grid>
                    <Button type="submit" fullWidth variant="contained" color="primary" className={classes.submit}>
                        {isSignup ? 'Sign Up' : 'Sign In'}
                    </Button>
                    <GoogleLogin 
                        clientId="1083978820758-pedv6cbflbof0lefhou5csc4n6vefnpg.apps.googleusercontent.com"
                        render={(renderProps) =>(
                            <Button fullWidth variant="contained" color="primary" className={classes.googleButton}  onClick={renderProps.onClick} disabled={renderProps.disabled} startIcon={<Icon />}     >
                               Google Sign In
                             </Button>
                            
                        )}
                        onSuccess={GoogleSuccess}
                        onFailure={GoogleFailure}
                        cookiePolicy="single_host_origin"

                    />
                    <Grid container justify="flex-end">
                        <Grid item>
                            <Button onClick={swithcMode}>
                                { isSignup ? 'Already have an account? Sign In' : 'Don\'t have account? Sign Up ' }
                            </Button>
                        </Grid>

                    </Grid>
                </form>
            </Paper>
        </Container>
    )
}

export default Auth
